import React, { useState } from "react";
import MaxWidthWrapper from "@/components/MaxWidthWrapper";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Dialog, DialogContent, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Check, Plus, CreditCard, Users, PiggyBank, Receipt } from "lucide-react";

type User = {
  id: number;
  name: string;
  balance: number;
};

type Expense = {
  id: number;
  description: string;
  amount: number;
  paidBy: number;
  splitBetween: number[];
};

type Payment = {
  id: number;
  amount: number;
  paidBy: number;
  splitBetween: number[];
};

export default function Page() {
  const [users, setUsers] = useState<User[]>([
    { id: 1, name: "Aksh", balance: 0 },
    { id: 2, name: "John", balance: 0 },
    { id: 3, name: "Jane", balance: 0 },
  ]);
  const [expenses, setExpenses] = useState<Expense[]>([]);
  const [payments, setPayments] = useState<Payment[]>([]);
  const [description, setDescription] = useState("");
  const [amount, setAmount] = useState<string>("");
  const [paidBy, setPaidBy] = useState<number | "">("");
  const [splitBetween, setSplitBetween] = useState<number[]>([]);
  const [newUserName, setNewUserName] = useState("");
  const [activeTab, setActiveTab] = useState("addExpense");

  const handleAddUser = () => {
    if (newUserName.trim() !== "") {
      const newUser: User = {
        id: users.length + 1,
        name: newUserName,
        balance: 0,
      };
      setUsers([...users, newUser]);
      setNewUserName("");
    }
  };

  const handleAddExpense = () => {
    if (description && amount && paidBy && splitBetween.length > 0) {
      const newExpense: Expense = {
        id: expenses.length + 1,
        description,
        amount: parseFloat(amount),
        paidBy: +paidBy,
        splitBetween,
      };
      const splitAmount = parseFloat(amount) / splitBetween.length;
      const updatedUsers = users.map((user) => {
        if (user.id === paidBy) {
          return {
            ...user,
            balance: user.balance + (parseFloat(amount) - splitAmount),
          };
        }
        if (splitBetween.includes(user.id)) {
          return { ...user, balance: user.balance - splitAmount };
        }
        return user;
      });
      setExpenses([...expenses, newExpense]);
      setUsers(updatedUsers);
      setDescription("");
      setAmount("");
      setPaidBy("");
      setSplitBetween([]);
    }
  };

  const handleToggleSplitUser = (userId: number) => {
    if (splitBetween.includes(userId)) {
      setSplitBetween(splitBetween.filter((id) => id !== userId));
    } else {
      setSplitBetween([...splitBetween, userId]);
    }
  };

  const calculateSettlement = () => {
    const payMap: { [key: number]: number } = {};
    const receiveMap: { [key: number]: number } = {};
    users.forEach((user) => {
      if (user.balance < 0) {
        payMap[user.id] = Math.abs(user.balance);
      } else if (user.balance > 0) {
        receiveMap[user.id] = user.balance;
      }
    });
    const settlements: { payer: User; receiver: User; amount: number }[] = [];
    Object.keys(payMap).forEach((payerId) => {
      let payerAmount = payMap[+payerId];
      Object.keys(receiveMap).forEach((receiverId) => {
        if (payerAmount > 0 && receiveMap[+receiverId] > 0) {
          const receiverAmount = receiveMap[+receiverId];
          const amountToSettle = Math.min(payerAmount, receiverAmount);
          settlements.push({
            payer: users.find((u) => u.id === +payerId)!,
            receiver: users.find((u) => u.id === +receiverId)!,
            amount: amountToSettle,
          });
          payMap[+payerId] -= amountToSettle;
          receiveMap[+receiverId] -= amountToSettle;
        }
      });
    });
    return settlements;
  };

  const settlements = calculateSettlement();

  const tabItems = [
    { value: "addExpense", icon: CreditCard, label: "Add Expense" },
    { value: "addUser", icon: Users, label: "Add User" },
    { value: "settlements", icon: PiggyBank, label: "Settlements" },
    { value: "payments", icon: Receipt, label: "Payments" },
  ];

  return (
    <div className="min-h-[calc(100vh-64px)] bg-gray-50 dark:bg-inherit">
      <MaxWidthWrapper className="min-w-[60vw]">
        <div className="h-[calc(100vh-64px)] grid place-content-center">
          <Card className="max-w-5xl w-full h-[80vh] border-2 border-green-600 dark:border-rose-800 rounded-xl shadow-lg p-4">
            <div className="flex gap-4 mb-4">
              {tabItems.map((tab) => (
                <Button
                  key={tab.value}
                  variant={activeTab === tab.value ? "solid" : "outline"}
                  onClick={() => setActiveTab(tab.value)}
                >
                  <tab.icon className="w-4 h-4 mr-2" />
                  {tab.label}
                </Button>
              ))}
            </div>
            <div>
              {activeTab === "addExpense" && (
                <div>
                  <h3 className="text-lg font-semibold mb-4">Add Expense</h3>
                  <Input
                    placeholder="Description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    className="mb-2"
                  />
                  <Input
                    placeholder="Amount"
                    type="number"
                    value={amount}
                    onChange={(e) => setAmount(e.target.value)}
                    className="mb-2"
                  />
                  <div className="mb-4">
                    <label className="block text-sm font-medium mb-2">Paid By:</label>
                    <select
                      value={paidBy}
                      onChange={(e) => setPaidBy(parseInt(e.target.value))}
                      className="w-full border rounded p-2"
                    >
                      <option value="">Select a User</option>
                      {users.map((user) => (
                        <option key={user.id} value={user.id}>
                          {user.name}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium mb-2">Split Between:</label>
                    {users.map((user) => (
                      <div key={user.id} className="flex items-center mb-1">
                        <input
                          type="checkbox"
                          checked={splitBetween.includes(user.id)}
                          onChange={() => handleToggleSplitUser(user.id)}
                          className="mr-2"
                        />
                        {user.name}
                      </div>
                    ))}
                  </div>
                  <Button onClick={handleAddExpense} className="mt-4">
                    Add Expense
                  </Button>
                </div>
              )}
              {activeTab === "addUser" && (
                <div>
                  <h3 className="text-lg font-semibold mb-4">Add User</h3>
                  <Input
                    placeholder="Enter user name"
                    value={newUserName}
                    onChange={(e) => setNewUserName(e.target.value)}
                    className="mb-2"
                  />
                  <Button onClick={handleAddUser}>Add User</Button>
                </div>
              )}
              {activeTab === "settlements" && (
                <div>
                  <h3 className="text-lg font-semibold mb-4">Settlements</h3>
                  {settlements.length > 0 ? (
                    settlements.map((settlement, index) => (
                      <p key={index}>
                        {settlement.payer.name} owes {settlement.receiver.name} ${settlement.amount.toFixed(2)}
                      </p>
                    ))
                  ) : (
                    <p>No settlements needed.</p>
                  )}
                </div>
              )}
              {activeTab === "payments" && (
                <div>
                  <h3 className="text-lg font-semibold mb-4">Payments</h3>
                  <ul>
                    {payments.map((payment) => (
                      <li key={payment.id}>
                        ${payment.amount.toFixed(2)} paid by{" "}
                        {users.find((u) => u.id === payment.paidBy)?.name} and split with{" "}
                        {payment.splitBetween
                          .map((id) => users.find((u) => u.id === id)?.name)
                          .join(", ")}
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </Card>
        </div>
      </MaxWidthWrapper>
    </div>
  );
}
